import React from 'react';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

ReactDOM.render(
  <React.StrictMode>
    <Wordle />
  </React.StrictMode>,
  document.getElementById('root')
);

function Wordle() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Switch>
        <Route exact path="/">
          <h1>Hello Wordle</h1>
        </Route>
        <Route path="/beebzdle">
          <App
            gameName="Beebzdle"
            customDictionary={["larby", "beebz", "poopy", "ricks"]}
            solution="RICKS"
          />
        </Route>
        <Route path="/discordle">
          <App
            gameName="Discordle"
            customDictionary={["weebs", "furry", "degen"]}
            solution="DEGEN"
          />
        </Route>
        <Route path="/kimatraidle">
          <App
            gameName="Kimatraidle"
            customDictionary={["anuja", "kiran", "aysha", "sindh"]}
            solution="SINDH"
          />
        </Route>
        <Route path="/thawardle">
          <App
            gameName="Thawardle"
            customDictionary={["hathi", "kirti", "radha", "kiran", "nanis", "dadis"]}
            solution="HATHI"
          />
        </Route>
      </Switch>
    </Router>
  )
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
